import React,{useEffect,useState} from 'react'
import axios from 'axios'
import {CCard,CDataTable,CBadge,CButton,CCollapse,CCardBody} from '@coreui/react'
const UserPpat = () =>{
   
  const [data,setData] = useState([])
    
  


  const fields = [
    { key: 'NO', _style: { width: '10%',textAlign:'center'},filter: false },
    { key: 'NIK', _style: { width: '10%',textAlign:'center'},filter: false },
    { key: 'nama_lengkap', _style: { width: '30%',textAlign:'center'} },
    { key: 'alamat', _style: { width: '20%',textAlign:'center'} },
    { key: 'email', _style: { width: '20%',textAlign:'center'} },
  
  ]

  
    useEffect(()=>{
        axios.get("https://kantahsalatiga.com/rest-api/ppat/userppat/user.php")
            .then((res)=>{
                if (res.data.message === "No post found") {
                    setData([])
                } else {
                    setData(res.data)
                }
            })
    },[])
    return(
        <CCard style={{padding:20}}>
              <CDataTable
      items={data}
      fields={fields}
      columnFilter
      tableFilter
      footer
      itemsPerPageSelect
      itemsPerPage={5}
      hover
      sorter
      pagination
      scopedSlots = {{
        'NIK':  
        (item)=>(
            <td>
              {item.nik}
            </td>
          )
        ,
          'NO':
          (item,index)=>(
            <td style={{textAlign:'center'}}>
                {index+1}
            </td>
          )
      }}
    
    />
      
        </CCard>
        
    )
}

export default UserPpat;